let dataObj = {}
export default {
    onLoad(option){
        console.log(option)
        dataObj = {
            ...option,
            sort:'zonghe',
            page:1
        }
        this.getallgoods(dataObj)
    },
    data(){
        return {
            loadingType:0,
            showIndex:1,
            filteAl: 0,
            goodsList:[],
            isshow:false
        }
    },
    onReachBottom:function(){
        // 上拉加载更多
        if(this.loadingType == 1 || this.loadingType == 2) { return false }
        this.loadingType = 1
        
        dataObj.page++
		this.getallgoods(dataObj)
	},
    methods: {
        searchIndex(index,str){
            this.showIndex = index
            dataObj.sort = str
            dataObj.page = 1
            this.goodsList = []
            this.loadingType = 0
            if (index === 3) {
                if (this.filteAl === 1) {
                    this.filteAl = 2
                    dataObj.sort = 'height_low'
                } else {
                    this.filteAl = 1
                    dataObj.sort = 'low_height'
                }
            }
             this.$loading()
            this.getallgoods(dataObj)
           
        },
        getallgoods(data){
            this.$http.getallgoods(data).then(res=>{
                if(res.status == 200) {
                        
                        if(res.data.length == 0) { 
                            if(this.goodsList.length == 0) {
                                this.isshow = true
                            }
                            this.loadingType = 2 
                            return false
                        }
                        this.goodsList = [...this.goodsList,...res.data] 
                        this.loadingType = 0 
                        if(this.goodsList.length == 0) {
                            this.isshow = true
                        }

                }
            })
        }
    },
}
